import axios from 'axios';
import React, { useEffect, useState } from 'react';
// import NumberEasing from 'react-number-easing';



const HomeContentComponent = () => {

    const [userCount, setUserCount] = useState(0);
    const [productCount, setProductCount] = useState(0);
    const [tagCount, setTagCount] = useState(0);
    const [categoryCount, setCategoryCount] = useState(0);
    const [propertyCount, setPropertyCount] = useState(0);

    useEffect(() => {
        getPanelData();
    }, [])


    const getPanelData = () => {
        axios.post("http://localhost:5000/api/utility/getPanelData", {
            token: localStorage.getItem("token"),
        }).then((result) => {
            if (result.data.Success == true) {
                setUserCount(result.data.userCount);
                setProductCount(result.data.productCount);
                setTagCount(result.data.tagCount);
                setCategoryCount(result.data.categoryCount);
                setPropertyCount(result.data.propertyCount);
            }
        }).catch((err) => {
            console.log(err);
        });
    }

    return (
        <div className='row p-2'>

            <div className='col-md-6 col-lg-4 col-12 mb-2'>
                <div className='card'>
                    <div className='card-header'>تعداد کاربران</div>
                    <div className='card-body'>
                        <h1>
                            {/* <NumberEasing
                                value={userCount}
                                speed={3000}
                                ease='quintInOut' /> */}
                        </h1>
                    </div>
                </div>
            </div>
            <div className='col-md-6 col-lg-4 col-12 mb-2'>
                <div className='card'>
                    <div className='card-header'>تعداد محصولات</div>
                    <div className='card-body'>
                        <h1>
                            {/* <NumberEasing
                                value={productCount}
                                speed={3000}
                                ease='quintInOut' /> */}
                        </h1>
                    </div>
                </div>
            </div>

            <div className='col-md-6 col-lg-4 col-12 mb-2'>
                <div className='card'>
                    <div className='card-header'>تعداد تگ ها</div>
                    <div className='card-body'>
                        <h1>
                            {/* <NumberEasing
                                value={tagCount}
                                speed={3000}
                                ease='quintInOut' /> */}
                        </h1>
                    </div>
                </div>
            </div>

            <div className='col-md-6 col-lg-4 col-12 mb-2'>
                <div className='card'>
                    <div className='card-header'>تعداد دسته بندی ها</div>
                    <div className='card-body'>
                        <h1>
                            {/* <NumberEasing
                                value={categoryCount}
                                speed={3000}
                                ease='quintInOut' /> */}
                        </h1>
                    </div>
                </div>
            </div>

            <div className='col-md-6 col-lg-4 col-12 mb-2'>
                <div className='card'>
                    <div className='card-header'>تعداد خصوصیات</div>
                    <div className='card-body'>
                        <h1>
                            {/* <NumberEasing
                                value={propertyCount}
                                speed={3000}
                                ease='quintInOut' /> */}
                        </h1>
                    </div>
                </div>
            </div>

            <div className='col-md-6 col-lg-4 col-12 mb-2'>
                <div className='card'>
                    <div className='card-header'>Header</div>
                    <div className='card-body'>
                        <h1>hello</h1>
                    </div>
                </div>
            </div>



        </div>
    )
}

export default HomeContentComponent