import React from 'react'
import AddProductContentComponent from '../contentComponents/AddProductContentComponent'
import SidebarComponent from '../contentComponents/SportComponent'

const AddProductComponent = () => {
    return (
        <div className='container-fluid '>
            <div className='row'>
                <div className='col-lg-3 col-12 d-lg-block d-none bg-danger vh-100 border-danger'>
                    <SidebarComponent />
                </div>
                <div className='col-lg-9 col-12 bg-info vh-100'>
                    <AddProductContentComponent/>
                </div>
            </div>
        </div>
    )
}

export default AddProductComponent