import React from 'react'
import CategoryContentComponent from '../contentComponents/CategoryContentComponent'
import SidebarComponent from '../contentComponents/SportComponent'

const CategoryComponent = () => {
    return (
        <div className='container-fluid '>
            <div className='row'>
                <div className='col-lg-3 col-12 d-lg-block d-none bg-danger vh-100'>
                    <SidebarComponent />
                </div>
                <div className='col-lg-9 col-12 bg-info vh-100'>
                    <CategoryContentComponent />
                </div>
            </div>
        </div>
    )
}

export default CategoryComponent