import React from 'react';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import AboutScreen from '../screens/AboutScreen';
import HomeScreen from '../screens/HomeScreen';
import MainScreen from '../screens/MainScreen';
import LoginScreen from '../screens/LoginScreen';
import Header from './HeaderComponent';
import { useEffect } from 'react';
import { useState } from 'react';
import Page404Screen from '../screens/Page404Screen';
import AddProductScreen from '../screens/AddProductScreen';
import UserListScreen from '../screens/UserListScreen';
import RegisterScreen from '../screens/RegisterScreen';
import EmailVerifyScreen from '../screens/EmailVerifyScreen';
import ProfileScreen from '../screens/ProfileScreen';
import TagScreen from '../screens/TagScreen';
import CategoryScreen from '../screens/CategoryScreen';
import PropertyScreen from '../screens/PropertyScreen';
import ProductDetailScreen from '../screens/ProductDetailScreen';
import SearchBarComponent from './SearchBarComponent';
import FooterComponent from './FooterComponent';


function Router() {

    const [loggedIn, setLoggedIn] = useState("loading");

    useEffect(() => {
        if (localStorage.getItem("token")) {
            setLoggedIn(true);
        }
        else {
            setLoggedIn(false);
        }
    }, [])


    return (
        <>
            <BrowserRouter>
                {/* <Header /> */}
                {/* <SearchBarComponent/> */}
                <Routes>

                    <Route path='/productDetail/:keyword' element={<ProductDetailScreen />}></Route>
                    <Route path='/tags' element={<TagScreen />}></Route>
                    <Route path='/properties' element={<PropertyScreen />}></Route>
                    <Route path='/categories' element={<CategoryScreen />}></Route>
                    <Route path='/addProduct' element={<AddProductScreen />}></Route>
                    <Route path='/userList' element={<UserListScreen />}></Route>

                    <Route path='/login' element={<LoginScreen />}></Route>
                    <Route path='/register' element={<RegisterScreen />}></Route>
                    <Route path='/verifyEmail' element={<EmailVerifyScreen />}></Route>
                    <Route path='/profile' element={<ProfileScreen />}></Route>
                    <Route path='/home' element={<HomeScreen />} ></Route>
                    <Route path='/' element={<MainScreen />} ></Route>
                    <Route path='/about' element={<AboutScreen />} ></Route>
                    <Route path='*' element={<Page404Screen />}></Route>
                </Routes>
                {/* {
                    localStorage.getItem("token") &&
                    <>
                        <FooterComponent />
                    </>
                } */}
            </BrowserRouter>
        </>
    )
};


export default Router;