import React from 'react'
import SidebarComponent from '../contentComponents/SportComponent'
import UserListContentComponent from '../contentComponents/UserListContentComponent'

const UserListComponent = () => {
  return (
    <div className='container-fluid '>
    <div className='row'>
        <div className='col-lg-3 col-12 d-lg-block d-none bg-danger vh-100'>
            <SidebarComponent />
        </div>
        <div className='col-lg-9 col-12 bg-info vh-100'>
            <UserListContentComponent/>
        </div>
    </div>
</div>
  )
}

export default UserListComponent