import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import Loading from '../components/LoadingComponent';
import LoginComponent from '../components/LoginComponent';
import HomeComponent from '../components/HomeComponent';

import axios from 'axios';
import EmailVerifyComponent from '../components/EmailVerifyComponent';

const EmailVerifyScreen = () => {

    const navigate = useNavigate();
    const [loggedIn, setLoggedIn] = useState("loading");



    useEffect(() => {
        setLoggedIn("loading");
        if (localStorage.getItem("token")) {

            axios.post("http://localhost:5000/api/auth/checkToken", {
                token: localStorage.getItem("token"),
            }).then((result) => {
                if (result.data.Success == true) {
                    if (result.data.Verified == true) {
                        setLoggedIn("yes");
                        navigate("/home");                        
                    }
                    else
                    {
                        setLoggedIn("unverified");
                    }
                }
                else {
                //     alert("email or password is wrong");

                }
            }).catch((err) => {
                console.log(err);
            });
        }
        else {
            localStorage.setItem("getPage", "/home");
            navigate("/login");
            setLoggedIn("no");
        }
    }, []);


    return (

        <>
            {
                loggedIn == "yes" &&
                <HomeComponent />
            }

            {
                loggedIn == "no" &&
                <LoginComponent />
            }

            {
                loggedIn == "loading" &&
                <Loading />
            }

            {
                loggedIn == "unverified" &&
                <EmailVerifyComponent />
            }
        </>
    )
}

export default EmailVerifyScreen