import axios from 'axios';
import React, { useEffect } from 'react'
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Loading from '../components/LoadingComponent'
import LoginComponent from '../components/LoginComponent'
import TagComponent from '../components/TagComponent'

const TagScreen = () => {

    const navigate = useNavigate();
    const [loggedIn, setLoggedIn] = useState("loading");

    useEffect(() => {
        setLoggedIn("loading");
        localStorage.setItem("getPage", "/userList");

        if (localStorage.getItem("token")) {

            axios.post("http://localhost:5000/api/auth/checkToken", {
                token: localStorage.getItem("token"),
            }).then((result) => {
                if (result.data.Success == true) {
                    if (result.data.expired == true) {
                        navigate("/login");
                        localStorage.removeItem("token");
                        localStorage.removeItem("getPage");
                    }
                    else {
                        if (result.data.Verified == false) {
                            setLoggedIn("unverified");
                            navigate('/verifyEmail');
                        }
                        else {
                            setLoggedIn("yes");
                            navigate("/tags");
                        }
                    }
                }
                else {
                    navigate("/login");
                }
            }).catch((err) => {
                console.log(err);
            });
        }
        else {

            navigate("/login");
            setLoggedIn("no");
        }
    }, []);



    return (
        <>
            {
                loggedIn == "no" &&
                <LoginComponent />
            }

            {
                loggedIn == "yes" &&
                <TagComponent />
            }

            {
                loggedIn == "loading" &&
                <Loading />
            }
        </>
    )
}

export default TagScreen