import axios from 'axios';
import React, { useEffect, useState } from 'react'
import ReactPaginate from 'react-paginate';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {faCheck , faUserCheck , faUserLock} from '@fortawesome/free-solid-svg-icons';


const UserListContentComponent = () => {

    const [userList, setUserList] = useState([]);
    const [totalPage, setTotalPage] = useState(0);
    const [currentPage , setCurrentPage] = useState(0);


    const getUserList = (page) => {
        axios.post("http://localhost:5000/api/auth/getUserList", {
            token: localStorage.getItem("token"),
            page:page
        }).then((result) => {
            console.log(result.data);
            setUserList(result.data.userList.docs);
            setTotalPage(result.data.userList.totalPages);
        }).catch((err) => {
            console.log(err);
        });
    }

    useEffect(() => {
        getUserList();
    }, [])

    const changePage = (e) => {
        getUserList(e.selected + 1)
    }



    return (
        <div className='row p-2'>
            <table className="table table-hover table-striped table-bordered bg-white">
                <thead>
                    <tr className='text-white bg-primary'>
                        <th>نام</th>
                        <th>ایمیل</th>
                        <th>وضعیت</th>
                        <th>تعداد محصول</th>

                    </tr>
                </thead>
                <tbody>
                    {
                        userList.map((item, index) => {
                            return (
                                <tr key={index}>
                                    <td style={{width:'25%'}}>{item.userName}</td>
                                    <td style={{width:'50%'}}>{item.email}</td>
                                    {
                                        item.emailVerified == true &&
                                        <td className='text-center'>
                                            <FontAwesomeIcon color='green' size={'2x'} icon={faUserCheck}/>
                                        </td>
                                    }
                                    {
                                        item.emailVerified == false &&
                                        <td className='text-center'>
                                            <FontAwesomeIcon color='red' size={'2x'} icon={faUserLock} />
                                        </td>
                                    }
                                    <td>{item.products.length}</td>
                                </tr>
                            )
                        }).reverse()
                    }
                </tbody>
            </table>


            <div style={{direction:'ltr' , alignSelf:'center'}} className="row" >
                <ReactPaginate 
                    breakLabel="..."
                    className='mystyle'
                    nextLabel="بعد"
                    onPageChange={(e)=> changePage(e)}
                    pageRangeDisplayed={1}
                    pageCount={parseInt(totalPage)}
                    previousLabel="قبل"
                    renderOnZeroPageCount={null}
                    containerClassName={'pagination'}
                    pageClassName={'page-link'}
                    previousClassName={'page-item'}
                    previousLinkClassName={'page-link'}
                    nextClassName={'page-item'}
                    nextLinkClassName={'page-link'}
                    activeClassName={'paginateactive'}
                    breakClassName={'page-item'}
                    breakLinkClassName={'page-link'}
                    initialPage={currentPage}
                >
                </ReactPaginate>
            </div>


        </div>
    )
}

export default UserListContentComponent