import React, { useEffect, useRef, useState } from 'react'
import axios from 'axios';
import ReactPaginate from 'react-paginate';

const PropertyContentComponent = () => {

  const [title, setTitle] = useState();
  const [propertyList, setPropertyList] = useState([]);
  const [totalPage, setTotalPage] = useState(0);
  const [currentPage, setCurrentPage] = useState(0);
  const titleInput = useRef(null);
  const [propertyCount, setPropertyCount] = useState(0);


  useEffect(() => {
    getPropertyList();
    getPanelData();

  }, []);



  const getPanelData = () => {
    axios.post("http://localhost:5000/api/utility/getPanelData", {
      token: localStorage.getItem("token"),
    }).then((result) => {
      if (result.data.Success == true) {
        setPropertyCount(result.data.propertyCount);
      }
    }).catch((err) => {
      console.log(err);
    });
  }


  const getPropertyList = (page) => {
    axios.post("http://localhost:5000/api/property/getPropertyList", {
      page: page,
      token: localStorage.getItem("token"),
    }).then((result) => {

      console.log(result.data);
      setPropertyList(result.data.propertyList.docs);
      setTotalPage(result.data.propertyList.totalPages);
    }).catch((err) => {
      console.log(err);
    });
  }

  const handleStore = () => {
    if (title == "" || title == null) {
      alert("Enter title of property");
      return;
    }

    axios.post("http://localhost:5000/api/property/addProperty", {
      title: title,
      token: localStorage.getItem("token"),
    }).then((result) => {
      if (title == null || title == "") {
        alert("Enter value of title");
        return;
      }
      if (result.data.code == 400) {
        alert(result.data.Message);
      }
      else {
        getPropertyList();
        getPanelData();
        clearInput();
      }
    }).catch((err) => {
      console.log(err);
    });
  }

  const changePage = (e) => {
    getPropertyList(e.selected + 1)
  }

  const clearInput = () => {
    getPropertyList(totalPage);
    titleInput.current.value = "";
    setTitle(null);
  }


  return (
    <div className='container mt-3'>
      <div className='row p-2 border border-primary border-3 rounded'>
        <div className='col-md-6 mb-3'>
          <div className='form-group'>
            <label htmlFor='title'>خصوصیت</label>
            <input ref={titleInput} value={title ? title : ""} onChange={(event) => setTitle(event.target.value)} type={"text"} id='title' className='form-control' />
          </div>
        </div>
        <div className='col-12 mb-3'>
          <button onClick={() => handleStore()} className='btn btn-danger'>ثبت</button>
        </div>
      </div>

      <div className='mt-3 rounded'>
        <span className='badge bg-warning text-dark'>
          { 'تعداد خصوصیات ثبت شده ' + propertyCount}
        </span>
      </div>

      <div className='row mt-3'>
        <table className="table table-hover table-striped table-bordered bg-white">
          <thead className='bg-primary text-white'>
            <tr>
              <th>#</th>
              <th>نام خصوصیت</th>
              <th>تعداد زیر مجموعه</th>
            </tr>
          </thead>
          <tbody>
            {
              propertyList.map((item, index) => {
                return (
                  <tr key={index}>
                    <td>{index + 1}</td>
                    <td>{item.title}</td>
                    <td>{item.products.length}</td>
                  </tr>
                )
              })
            }
          </tbody>
        </table>

        <div className='row' style={{ direction: 'ltr', alignSelf: 'center' }}>
          <ReactPaginate
            breakLabel="..."
            className='mystyle'
            nextLabel="بعد"
            onPageChange={(e) => changePage(e)}
            pageRangeDisplayed={1}
            pageCount={parseInt(totalPage)}
            previousLabel="قبل"
            renderOnZeroPageCount={null}
            containerClassName={'pagination'}
            pageClassName={'page-link'}
            previousClassName={'page-item'}
            previousLinkClassName={'page-link'}
            nextClassName={'page-item'}
            nextLinkClassName={'page-link'}
            activeClassName={'paginateactive'}
            breakClassName={'page-item'}
            breakLinkClassName={'page-link'}
            initialPage={currentPage}
          >
          </ReactPaginate>
        </div>

      </div>
    </div>
  )
}

export default PropertyContentComponent