import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { useRef } from 'react';
import ReactPaginate from 'react-paginate';
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

const CategoryContentComponent = () => {

    const [title, setTitle] = useState("");
    const titleInput = useRef(null);
    const MySwal = withReactContent(Swal);
    const [categoryList, setCategoryList] = useState([]);
    const [totalPage, setTotalPage] = useState(0);
    const [currentPage, setCurrentPage] = useState(0);

    useEffect(() => {
        getCategoryList();
    }, [])


    const handleStore = () => {

        if (title == "" || title == null) {
            alert("Enter the title of category");
            return;
        }

        axios.post("http://localhost:5000/api/category/addCategory", {
            title: title,
            token: localStorage.getItem("token"),
        }).then((result) => {
            if (title == null || title == "") {
                alert("Enter value of title");
                return;
            }
            if (result.data.code == 400) {
                alert(result.data.Message);
            }
            else {
                getCategoryList();
                clearInput();
            }
        }).catch((err) => {
            console.log(err);
        });
    }

    const changePage = (e) => {
        getCategoryList(e.selected + 1)
    }

    const clearInput = () => {
        getCategoryList(totalPage);
        titleInput.current.value = "";
        setTitle(null);
    }


    const getCategoryList = (page) => {
        axios.post("http://localhost:5000/api/category/getCategoryList", {
            page: page,
            token: localStorage.getItem("token"),
        }).then((result) => {
            console.log(result.data.categoryList.docs);
            setCategoryList(result.data.categoryList.docs);
            setTotalPage(result.data.categoryList.totalPages);
        }).catch((err) => {
            console.log(err);
        });
    }


    return (
        <div className='container mt-3'>
            <div className='row p-2 border border-primary border-3 rounded'>
                <div className='col-md-6 mb-3'>
                    <div className='form-group'>
                        <label htmlFor='title'>عنوان دسته بندی</label>
                        <input ref={titleInput} value={title ? title : ""} onChange={(event) => setTitle(event.target.value)} type={"text"} id='title' className='form-control' />
                    </div>
                </div>
                <div className='col-12 mb-3'>
                    <button onClick={() => handleStore()} className='btn btn-danger'>ثبت</button>
                </div>
            </div>

            <div className='row mt-3'>
                <table className="table table-bordered table-hover table-striped bg-white">
                    <thead>
                        <tr className='bg-primary text-white'>
                            <th>#</th>
                            <th>نام دسته بندی</th>
                            <th>تعداد زیر مجموعه</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            categoryList.map((item, index) => {
                                return (
                                    <tr key={index}>
                                        <td className='tableTitleSize' >{index + 1}</td>
                                        <td>{item.title}</td>
                                        <td>{item.products.length}</td>
                                    </tr>
                                )
                            })
                        }
                    </tbody>
                </table>

                <div style={{ direction: 'ltr', alignSelf: 'center' }} className="row" >
                    <ReactPaginate
                        breakLabel="..."
                        className='mystyle'
                        nextLabel="بعد"
                        onPageChange={(e) => changePage(e)}
                        pageRangeDisplayed={1}
                        pageCount={parseInt(totalPage)}
                        previousLabel="قبل"
                        renderOnZeroPageCount={null}
                        containerClassName={'pagination'}
                        pageClassName={'page-link'}
                        previousClassName={'page-item'}
                        previousLinkClassName={'page-link'}
                        nextClassName={'page-item'}
                        nextLinkClassName={'page-link'}
                        activeClassName={'paginateactive'}
                        breakClassName={'page-item'}
                        breakLinkClassName={'page-link'}
                        initialPage={currentPage}
                    >
                    </ReactPaginate>
                </div>


            </div>
        </div>
    )
}

export default CategoryContentComponent