import React, { useState } from 'react'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

    
const LoginComponent = () => {

    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const MySwal = withReactContent(Swal);
    const navigate = useNavigate();


    const handleLogin = async () => {
        if (email == null || email == "") {
            MySwal.fire({
                title: <p>Enter Email Address</p>,
                icon: 'error',
            })
            return;
        }

        if (password == "" || password == null) {
            MySwal.fire({
                title: <p>Enter Password</p>,
                icon: 'error',
            })
            return;
        }

        await axios.post("http://localhost:5000/api/auth/login", {
            email: email,
            password: password,
        }).then((result) => {
            console.log(result.data);
            localStorage.setItem("token", result.data.token);

            if(result.data.Success == false){
                MySwal.fire({
                    title: <p>Email or password is wrong</p>,
                    icon: 'error',
                })
            }
            else{
                axios.post("http://localhost:5000/api/auth/checkEmailVerified", {
                    email: email
                }).then((result) => {
    
    
    
                    if (result.data.Success == true) {
                        if (result.data.Verified == false) {
                            navigate('/verifyEmail');
                        }
                        else {
                            if (localStorage.getItem("getPage")) {
                                navigate(localStorage.getItem("getPage"));
                            }
                            else {
                                localStorage.removeItem("getPage");
                                navigate("/home");
                            }
                        }
                    }
    
    
                }).catch((err) => {
                    console.log(err);
                });
            }
        }).catch((err) => {
            console.log(err);
        });




    }


    return (
        <div className='container'>
            <div className='row d-flex justify-content-center px-2 mt-5'>
                <div className='col-md-6 col-12 border border-primary rounded p-3 d-flex flex-column gap-3'>
                    <div>
                        <h3 className='text-danger'>Login User!</h3>
                    </div>
                    <div className='form-group'>
                        <label htmlFor='email'>Email</label>
                        <input onChange={(event) => setEmail(event.target.value)} required type={"email"} id='email' className='form-control' />
                    </div>
                    <div className='form-group'>
                        <label htmlFor='password'>Password</label>
                        <input onChange={(event) => setPassword(event.target.value)} required type={"text"} id='password' className='form-control' />
                    </div>
                    <div className='d-flex justify-content-between'>
                        <button onClick={() => handleLogin()} className='btn btn-primary'>Login</button>
                        <button className='btn btn-danger'>Register</button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default LoginComponent