import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";
import BasketComponent from "./BasketComponent";
import {
  faCheck,
  faBasketShopping,
  faUserLock,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import TextField from "@mui/material/TextField";

const SearchBarComponent = () => {
  const [loggedIn, setLoggedIn] = useState("loading");
  const MySwal = withReactContent(Swal);

  useEffect(() => {
    setLoggedIn("loading");
    if (localStorage.getItem("token")) {
      console.log("has token");
      setLoggedIn("yes");
    } else {
      console.log("does not have token");
      setLoggedIn("no");
    }
  });

  const getBasketList = () => {
    if (localStorage.getItem("basket")) {
      let data = localStorage.getItem("basket");
      let arrayData = JSON.parse(data);
      if (arrayData) {
        MySwal.fire({
          title: <BasketComponent items={arrayData} />,
          // icon: 'error',
        }).then((result) => {});
      }
    }
  };

  return (
    <div className="flex gap-4 mx-5">
      <div>
        <h1 className="text-4xl mb-3 font-bold text-red-500">آلیشکده</h1>
       
      </div>
    </div>
  );
};

export default SearchBarComponent;
