import React, { useEffect , useState } from 'react'
import { Link } from 'react-router-dom';
import Swal from 'sweetalert2';
import withReactContent from 'sweetalert2-react-content';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

const SidebarComponent = () => {

    const MySwal = withReactContent(Swal);
    const navigate = useNavigate();

    const [userName , setUserName] = useState();
    const [userEmail , setUserEmail] = useState();


    // useEffect(()=> {
    //     getUserData();
    // } , [])

    const handleLogout = () => {

        MySwal.fire({
            title: <p>آیا میخواهید از سیستم خارج شوید؟</p>,
            icon: 'question',
            cancelButtonText: "خیر",
            showCancelButton: true,
            confirmButtonText: 'بله'
        }).then((event) => {
            if (event.isConfirmed == true) {
                localStorage.removeItem("token");
                localStorage.removeItem("getPage");
                navigate("/");
            }
        })
    }

    const getUserData = () => {
        axios.post("http://localhost:5000/api/utility/getUserData" , {
            token: localStorage.getItem("token"),
        }).then((result) => {
            // console.log(result.data);

            setUserName(result.data.user.userName);
            setUserEmail(result.data.user.email);

        }).catch((err) => {
            console.log(err);
        });
    }

    return (
        <nav className='p-0 m-0'>
            {/* <div className='custome-border pb-3'>
                <h3>
                    {
                        userName != null || userName != "" ? userName : "Loading"
                    }
                </h3>
                <h5>
                    {
                        userEmail != null || userEmail != "" ? userEmail : "Loading"
                    }
                </h5>
            </div> */}

            <ul className='sidebarStyle p-1 d-flex flex-column gap-3'>
                <Link to={"/home"} className='text-decoration-none text-white'>داشبرد من</Link>
                <Link to={"/userList"} className='text-decoration-none text-white'>لیست کاربران</Link>
                <Link to={"/addProduct"} className='text-decoration-none text-white'>ثبت محصول</Link>
                <Link to={"/profile"} className='text-decoration-none text-white'>پروفایل کاربر</Link>
                <Link to={"/tags"} className='text-decoration-none text-white'>تگ های محصول</Link>
                <Link to={"/categories"} className='text-decoration-none text-white'>دسته بندی ها</Link>
                <Link to={"/properties"} className='text-decoration-none text-white'>خصوصیات محصول</Link>

                <Link onClick={() => handleLogout()} className='text-decoration-none text-white'>خروج از سیستم</Link>
            </ul>
        </nav>
    )
}

export default SidebarComponent