import React, { useEffect } from 'react';
import axios from 'axios';
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import { useState } from 'react';

const ProfileContentComponent = () => {

    const [phoneNumber, setPhoneNumber] = useState();
    const [avatar, setAvatar] = useState();
    const MySwal = withReactContent(Swal);

    useEffect(() => {
        getProfile();
    }, [])


    const getProfile = () => {
        axios.post("http://localhost:5000/api/profile/getProfile", {
            token: localStorage.getItem("token"),
        }).then((result) => {

            setPhoneNumber(result.data.profile[0].phoneNumber);
            setAvatar(result.data.profile[0].avatar);
        }).catch((err) => {
            console.log(err);
        });
    }

    const handleStore = () => {
        if (phoneNumber == "" || phoneNumber == null) {
            MySwal.fire({
                title: <p>شماره همراه را وارد نمایید</p>,
                icon: 'error',
            })
            return;
        }
        if (avatar == null || avatar == "") {
            MySwal.fire({
                title: <p>تصویر را وارد نمایید</p>,
                icon: 'error',
            })
            return;
        }

        axios.post("http://localhost:5000/api/profile/updateProfile", {
            phoneNumber: phoneNumber,
            avatar: avatar,
            token: localStorage.getItem("token"),
        })
            .then((result) => {
                MySwal.fire({
                    title: <p>{result.data.Message}</p>,
                    icon: 'info',
                })
                getProfile();
            }).catch((err) => {
                console.log(err);
            });
    }

    return (
        <div className='container mt-3'>
            <div className='row p-2 border border-primary border-3 rounded'>
                <div className='col-md-6 mb-3'>
                    <div className='form-group'>
                        <label htmlFor='phoneNumber'>شماره همراه</label>
                        <input value={phoneNumber ? phoneNumber : ""} onChange={(event) => setPhoneNumber(event.target.value)} type={"number"} id='phoneNumber' className='form-control' />
                    </div>
                </div>
                <div className='col-md-6 mb-3'>
                    <div className='form-group'>
                        <label htmlFor='avatar'>تصویر</label>
                        <input onChange={(event) => setAvatar(event.target.value)} type={"file"} id='avatar' className='form-control' />
                    </div>
                </div>
                <div className='col-12 mb-3'>
                    <button onClick={() => handleStore()} className='btn btn-danger'>ثبت</button>
                </div>
            </div>
        </div>
    )
}

export default ProfileContentComponent