import axios from 'axios';
import React, { useEffect } from 'react'
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Loading from '../components/LoadingComponent'
import LoginComponent from '../components/LoginComponent'
import ProfileComponent from '../components/ProfileComponent'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

const ProfileScreen = () => {

    const [loggedIn, setLoggedIn] = useState("loading");
    const navigate = useNavigate();
    const MySwal = withReactContent(Swal);


    useEffect(() => {
        setLoggedIn("loading");
        localStorage.setItem("getPage", "/addProduct");

        if (localStorage.getItem("token")) {

            axios.post("http://localhost:5000/api/auth/checkToken", {
                token: localStorage.getItem("token"),
            }).then((result) => {
                if (result.data.Success == true) {
                    if (result.data.expired == true) {
                        navigate("/login");
                        localStorage.removeItem("token");
                        localStorage.removeItem("getPage");
                    }
                    else {
                        if (result.data.Verified == false) {
                            setLoggedIn("unverified");
                            navigate('/verifyEmail');
                        }
                        else {
                            setLoggedIn("yes");
                            localStorage.removeItem("getPage");
                            navigate("/profile");
                        }
                    }
                }
                else {
                    // MySwal.fire({
                    //     title: <p>صفحه منقضی شده است</p>,
                    //     icon: 'error',
                    // })

                    navigate("/login");
                    
                }
            }).catch((err) => {
                console.log(err);
            });
        }
        else {

            navigate("/login");
            setLoggedIn("no");
        }
    }, []);


    return (
        <>
            {
                loggedIn == "no" &&
                <LoginComponent />
            }

            {
                loggedIn == "yes" &&
                <ProfileComponent />
            }

            {
                loggedIn == "loading" &&
                <Loading />
            }
        </>
    )
}

export default ProfileScreen