import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import Loading from '../components/LoadingComponent';
import LoginComponent from '../components/LoginComponent';
import HomeComponent from '../components/HomeComponent';
import EmailVerifyScreen from './EmailVerifyScreen';
import axios from 'axios';



const HomeScreen = () => {

    const navigate = useNavigate();
    const [loggedIn, setLoggedIn] = useState("loading");


    useEffect(() => {
        setLoggedIn("loading");

        if (localStorage.getItem("token")) {

            axios.post("http://localhost:5000/api/auth/checkToken", {
                token: localStorage.getItem("token"),
            }).then((result) => {
                if(result.data.expired == true){
                    navigate("/login"); 
                    localStorage.removeItem("token");
                    localStorage.removeItem("getPage");
                }
                else{
                    if (result.data.Success == true) {
                        if (result.data.Verified == false) {
                            setLoggedIn("unverified");
                            navigate('/verifyEmail');
                        }
                        else {
                            setLoggedIn("yes");
                            navigate("/home");
                        }
                    }
                    else {
                        navigate("/login");
                    }
                }
                
            }).catch((err) => {
                console.log(err);
            });
        }
        else {
            // localStorage.setItem("getPage", "/home");
            localStorage.removeItem("getPage");
            navigate("/login");
            setLoggedIn("no");
        }
    }, []);




    return (

        <>
            {
                loggedIn == "yes" &&
                <HomeComponent />
            }

            {
                loggedIn == "no" &&
                <LoginComponent />
            }

            {
                loggedIn == "loading" &&
                <Loading />
            }

            {
                loggedIn == "unverified" &&
                <EmailVerifyScreen />
            }
        </>
    )
}

export default HomeScreen