import React, { useState } from 'react'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import { useNavigate } from 'react-router-dom';
import axios from 'axios';




const RegisterComponent = () => {

    const [userName, setUserName] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    let token = "this is my token value";
    const MySwal = withReactContent(Swal);
    const navigate = useNavigate();


    const handleLogin =  async()  => {

        if (userName == null || userName == "") {
            MySwal.fire({
                title: <p>نام خود را وارد نمایید</p>,
                icon: 'error',
            })
            return;
        }


        if (email == null || email == "") {
            MySwal.fire({
                title: <p>ایمیل خود را وارد نمایید</p>,
                icon: 'error',
            })
            return;
        }

        if (password == "" || password == null) {
            MySwal.fire({
                title: <p>Enter Password</p>,
                icon: 'error',
            })
            return;
        }


        await axios.post("http://localhost:5000/api/auth/register", {
            "userName": userName,
            "email": email,
            "password": password
        }).then((result) => {
            console.log(result.data);
            if(result.data.code == 400){
                MySwal.fire({
                    title: <p>کاربر قبلا ثبت نام شده است.</p>,
                    icon: 'error',
                })
                return;
            }
            else if(result.data.code == 401){
                MySwal.fire({
                    title: <p>{result.data.Message}</p>,
                    icon: 'error',
                })
                return;
            }
            else if(result.data.code == 200){
                localStorage.setItem("token" , result.data.token);
                navigate("/verifyEmail");
                axios.post("http://localhost:5000/api/auth/SendVerifyEmail" , {email:email}).then((result) => {
                    console.log(result.data);
                    MySwal.fire({
                        title: <p>کاربر با موفقیت ثبت شد</p>,
                        icon: 'info',
                    })

                    return;
                }).catch((err) => {
                    console.log(err);
                });
               
            }
        }).catch((err) => {
            console.log(err);
        });

        // localStorage.setItem("token", token);

        // if (localStorage.getItem("getPage")) {
        //     navigate(localStorage.getItem("getPage"));
        // }
        // else {
        //     navigate("/home")
        // }

    }


    return (
        <div className='container' >
            <div className='row d-flex justify-content-center px-2 mt-5'>
                <div className='col-md-6 col-12 border border-primary rounded p-3 d-flex flex-column gap-3'>
                    <div>
                        <h3 className='text-danger'>Register User!</h3>
                    </div>
                    <div className='form-group'>
                        <label htmlFor='userName'>UserName</label>
                        <input onChange={(event) => setUserName(event.target.value)} required type={"text"} id='userName' className='form-control' />
                    </div>
                    <div className='form-group'>
                        <label htmlFor='email'>Email</label>
                        <input onChange={(event) => setEmail(event.target.value)} required type={"email"} id='email' className='form-control' />
                    </div>
                    <div className='form-group'>
                        <label htmlFor='password'>Password</label>
                        <input onChange={(event) => setPassword(event.target.value)} required id='password' className='form-control' />
                    </div>
                    <div className='d-flex justify-content-between'>
                        <button onClick={() => handleLogin()} className='btn btn-primary'>ثبت نام</button>
                        <button className='btn btn-danger'>ورود به سیستم</button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default RegisterComponent