import React from 'react';
import HomeContentComponent from '../contentComponents/HomeContentComponent';

import SidebarComponent from '../contentComponents/SportComponent';


const HomeComponent = () => {       

    return (
        <div className='container-fluid'>
            <div className='row'>
                <div className='col-lg-3 col-12 d-lg-block d-none bg-danger' style={{height:2000}}>
                    <SidebarComponent/>
                </div>
                <div className='col-lg-9 col-12 bg-info vh-100'>
                    <HomeContentComponent/>
                </div>
            </div>
        </div>
    )
}

export default HomeComponent