import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom';

const EmailVerifyComponent = () => {

    const navigate = useNavigate();
    const [loggedIn, setLoggedIn] = useState("loading");


    useEffect(() => {
        setLoggedIn("loading");
        if (localStorage.getItem("token")) {

            axios.post("http://localhost:5000/api/auth/checkToken", {
                token: localStorage.getItem("token"),
            }).then((result) => {
                if (result.data.Success == true) {
                    if (result.data.Verified == true) {
                        setLoggedIn("yes");
                        navigate("/home");
                    }
                }
                else {
                    alert("email or password is wrong");
                }
            }).catch((err) => {
                console.log(err);
            });
        }
        else {
            localStorage.setItem("getPage", "/home");
            navigate("/login");
            setLoggedIn("no");
        }
    }, []);



    const handleLogout = () => {
        localStorage.removeItem("token");
        navigate('/');
    }

    const resendVerifyEmail = () => {
        if (localStorage.getItem("token")) {
            axios.post("http://localhost:5000/api/auth/sendMailByToken" , {
                token: localStorage.getItem("token"),
            }).then((result) => {
                console.log(result.data);
            }).catch((err) => {
                console.log(err);
            });
        }
        else{
            navigate("/login");
            setLoggedIn("no");
        }

    }

    return (
        <div className='container'>
            <div className='row d-flex justify-content-center px-2 mt-5'>
                <h1>تایید جیمیل</h1>
                <div className='col-6'>
                    <h3>we sent acccout verification for your gmail. please confirm that</h3>
                    <button onClick={() => handleLogout()} className='btn btn-danger'>Logout</button>
                    <button onClick={()=> resendVerifyEmail()} className="btn btn-info" >Send Again</button>
                </div>
            </div>
        </div>
    )
}

export default EmailVerifyComponent