import React, { useEffect, useRef, useState } from 'react';
import axios from 'axios';
import ReactPaginate from 'react-paginate';


const AddProductContentComponent = () => {

  const [productList, setProductList] = useState([]);
  const [totalPage, setTotalPage] = useState(0);
  const [currentPage, setCurrentPage] = useState(0);
  const [tagList, setTagList] = useState([]);
  const [categoryList, setCategoryList] = useState([]);
  const [propertyList, setPropertyList] = useState([]);
  const [selectedTags, setSelectedTags] = useState();
  const [selectedCategory, setSelectedCategory] = useState();
  const [selectedProperty, setSelectedProperty] = useState();
  const [storeType, setStoreType] = useState(false);
  const [storedTag, setStoredTag] = useState([]);
  const [storedCategory, setStoredCategory] = useState([]);
  const [storedProperty, setStoredProperty] = useState([]);
  const [productID, setProductID] = useState(null);

  const [name, setName] = useState("");
  const [price, setPrice] = useState(0);
  const [count, setCount] = useState(0);
  const [off, setOff] = useState(0);

  const nameInput = useRef(null);
  const priceInput = useRef(null);
  const countInput = useRef(null);
  const offInput = useRef(null);


  useEffect(() => {
    setStoreType(true);
    getProductList(0);
    getTagList();
    getCategoryList();
    getPropertyList();
  }, []);

  // const setImageFile = async (e) => {
  //   const stream = e.target.files[0].stream();
  //   // console.log(stream);
  //   const reader = stream.getReader();

  //   while (true) {
  //     const { done, value } = await reader.read();
  //     if (done) { break; }
  //     handleChunk(value);
  //   }
  // }

  // const handleChunk = (buf) => {
  //   console.log(buf);
  // }


  const getProductList = (page) => {
    axios.post("http://localhost:5000/api/product/getProductList", {
      page: page,
      token: localStorage.getItem("token"),
    }).then((result) => {
      console.log(result.data.productList.docs);
      setProductList(result.data.productList.docs);
      setTotalPage(result.data.productList.totalPages);
    }).catch((err) => {
      console.log(err);
    });
  }


  const getTagList = (page) => {
    axios.post('http://localhost:5000/api/tag/getTagList', {
      token: localStorage.getItem("token"),
    }).then((result) => {
      setTagList(result.data.tagList.docs);
    }).catch((err) => {
      console.log(err);
    });
  }


  const getCategoryList = (page) => {
    axios.post('http://localhost:5000/api/category/getCategoryList', {
      token: localStorage.getItem("token"),
    }).then((result) => {
      setCategoryList(result.data.categoryList.docs);
    }).catch((err) => {
      console.log(err);
    });
  }


  const getPropertyList = (page) => {
    axios.post('http://localhost:5000/api/property/getPropertyList', {
      token: localStorage.getItem("token"),
    }).then((result) => {
      setPropertyList(result.data.propertyList.docs);
    }).catch((err) => {
      console.log(err);
    });
  }


  const clearInput = () => {
    getProductList(totalPage);
    nameInput.current.value = "";
    priceInput.current.value = "";
    countInput.current.value = "";
    offInput.current.value = "";
    setName(null);
    setPrice(null);
    setCount(null);
    setOff(null);
  }


  const handleStore = () => {

    if (name == null || name == "") {
      alert("Enter name of product");
      return;
    }

    if (price == "" || price == null) {
      alert("Enter price of product");
      return;
    }

    if (count == null || count == "") {
      alert("Enter count of product");
      return;
    }

    if (off == null || off == "") {
      alert("Enter off for Product");
      return;
    }

    if (selectedTags == null || selectedTags == "") {
      alert("Select tag for your product");
      return;
    }


    if (selectedCategory == null || selectedCategory == "") {
      alert("Select category for your product");
      return;
    }


    axios.post("http://localhost:5000/api/product/addProduct", {
      name: name,
      price: price,
      count: count,
      off: off,
      token: localStorage.getItem("token"),
      tags: selectedTags,
      categories: selectedCategory,
      properties: selectedProperty,
    }).then((result) => {
      console.log(result.data);
      if (result.data.success == true && result.data.code == 200) {
        clearInput();
      }
      else {
        alert(result.data.Message);
      }
    }).catch((err) => {
      console.log(err);
    });
  }

  const changePage = (e) => {
    getProductList(e.selected + 1)
  }

  const getSelectedTag = (e) => {
    let value = Array.from(e.target.selectedOptions, option => option.value);
    setSelectedTags(value);
  }

  const getSelectedCategory = (e) => {
    let value = Array.from(e.target.selectedOptions, option => option.value);
    setSelectedCategory(value);
  }

  const getSelectedProperty = (e) => {
    let value = Array.from(e.target.selectedOptions, option => option.value);
    setSelectedProperty(value);
  }

  const getEditProduct = (productID) => {
    if (productID) {
      setStoreType(false);
    }
    else {
      setStoreType(true);
    }
    axios.post("http://localhost:5000/api/product/getEditProduct", {
      id: productID,
      token: localStorage.getItem("token"),
    }).then((result) => {
      setProductID(productID);
      setName(result.data.Product.name);
      setPrice(result.data.Product.price);
      setCount(result.data.Product.count);
      setOff(result.data.Product.off);
      setStoredTag(result.data.Tag);
      setStoredCategory(result.data.Category);
      setStoredProperty(result.data.Property);
    }).catch((err) => {
      console.log(err);
    });
  }

  
  const getUpdateProduct = () => {
    axios.post("http://localhost:5000/api/product/getUpdateProduct", {
      token: localStorage.getItem("token"),
      name: name,
      count: count,
      price: price,
      off: off,
      tags: selectedTags,
      productID: productID,
    }).then((result) => {
      console.log(result.data);
    }).catch((err) => {
      console.log(err);
    });
  }

  const getCancelUpdate = () => {
    setStoreType(true);
    setName("");
    setCount(0);
    setPrice(0);
    setOff(0);
    setStoredTag([]);
    setStoredCategory([]);
    setStoredProperty([]);
    setProductID(null);
  }


  return (
    <div className='container mt-3' >
      <div className='row border border-primary border-3 p-2 rounded'>



        <div className='col-md-6 mb-3'>
          <div className='form-group'>
            <label htmlFor='name'>نام محصول</label>
            <input value={name} ref={nameInput} type={"text"} id='name' className='form-control' onChange={(event) => setName(event.target.value)} />
          </div>
        </div>
        <div className='col-md-6 mb-3'>
          <div className='form-group'>
            <label htmlFor='price'>قیمت محصول</label>
            <input value={price} ref={priceInput} type={"number"} id='price' className='form-control' onChange={(event) => setPrice(event.target.value)} />
          </div>
        </div>
        <div className='col-md-6 mb-3'>
          <div className='form-group'>
            <label htmlFor='count'>تعداد</label>
            <input value={count} ref={countInput} type={"number"} id='count' className='form-control' onChange={(event) => setCount(event.target.value)} />
          </div>
        </div>
        <div className='col-md-6 mb-3'>
          <div className='form-group'>
            <label htmlFor='off'>تخفیف</label>
            <input value={off} ref={offInput} type={"number"} id='off' className='form-control' onChange={(event) => setOff(event.target.value)} />
          </div>
        </div>
        <div className='col-md-6 mb-3'>
          <div className='form-group'>
            <label htmlFor='off'>تگ ها</label>
            <select onChange={(event) => getSelectedTag(event)} multiple className='form-control'>
              {storedTag.length == 0 &&
                tagList.map((item, index) => {
                  return (
                    <option key={index} value={item._id}>{item.title}</option>
                  )
                })
              }
              {storedTag.length != 0 &&
                storedTag.map((item, index) => {
                  return (
                    <option selected key={index} value={item._id}>{item.title}</option>
                  )
                })
              }
            </select>
          </div>
        </div>

        <div className='col-md-6 mb-3'>
          <div className='form-group'>
            <label htmlFor='off'>دسته بندی ها</label>
            <select onChange={(event) => getSelectedCategory(event)} multiple className='form-control'>
              {storedCategory.length == 0 &&
                categoryList.map((item, index) => {
                  return (
                    <option key={index} value={item._id}>{item.title}</option>
                  )
                })
              }
              {storedCategory.length != 0 &&
                storedCategory.map((item, index) => {
                  return (
                    <option selected key={index} value={item._id}>{item.title}</option>
                  )
                })
              }
            </select>
          </div>
        </div>

        <div className='col-md-6 mb-3'>
          <div className='form-group'>
            <label htmlFor='off'>لیست خصوصیات</label>
            <select onChange={(event) => getSelectedProperty(event)} multiple className='form-control'>
              {storedProperty.length == 0 &&
                propertyList.map((item, index) => {
                  return (
                    <option key={index} value={item._id}>{item.title}</option>
                  )
                })
              }
              {storedProperty.length != 0 &&
                storedProperty.map((item, index) => {
                  return (
                    <option selected key={index} value={item._id}>{item.title}</option>
                  )
                })
              }
            </select>
          </div>
        </div>



        <div className='col-12'>
          {
            storeType == true &&
            <button onClick={() => handleStore()} className='btn btn-danger'>ثبت</button>
          }
          {
            storeType == false &&
            <div className='d-flex gap-2'>
              <button onClick={() => getUpdateProduct()} className='btn btn-warning'>ویرایش</button>
              <button onClick={() => getCancelUpdate()} className='btn btn-primary'>انصراف</button>
            </div>

          }


        </div>
      </div>


      <div className='mt-3'>
        <table className="table  table-bordered  bg-white">
          <thead>
            <tr className='text-white bg-primary'>
              <th>نام</th>
              {/* <th>ثبت توسط</th> */}
              {/* <th>تعداد تگ</th> */}
              {/* <th>لیست تگ</th> */}
              {/* <th>تعداد دسته بندی</th> */}
              {/* <th>لیست دسته بندی</th> */}
              {/* <th>تعداد خصوصیات</th> */}
              {/* <th>لیست خصوصیات</th> */}
              <th>قیمت</th>
              <th>تعداد</th>
              <th>تخفیف</th>
              <th>عملیات</th>
            </tr>
          </thead>
          <tbody>
            {
              productList.map((item, index) => {
                return (
                  <tr key={index}>
                    <td className=''>{item.name}</td>
                    {/* <td className=''>{item.user.userName}</td> */}
                    {/* <td className=''>{item.tags.length}</td> */}
                    {/* <td className=' d-flex flex-column align-items-center gap-1'>
                      {
                        item.tags.map((item, index) => {
                          return (
                            <span className='text-primary border border-primary p-1 rounded' key={index}>
                              {item.title}
                            </span>
                          )
                        })
                      }
                    </td> */}
                    {/* <td className=''>{item.categories.length}</td> */}
                    {/* <td className=' d-flex flex-column align-items-center gap-1'>
                      {
                        item.categories.map((item, index) => {
                          return (
                            <span className='text-primary border border-primary p-1 rounded' key={index}>
                              {item.title}
                            </span>
                          )
                        })
                      }
                    </td> */}

                    {/* <td className=''>{item.properties.length}</td> */}

                    {/* <td className=' d-flex flex-column align-items-center gap-1'>
                      {
                        item.properties.map((item, index) => {
                          return (
                            <span className='text-primary border border-primary p-1 rounded' key={index}>
                              {item.title}
                            </span>
                          )
                        })
                      }
                    </td> */}


                    <td className=''>{item.price}</td>
                    <td className=''>{item.count}</td>
                    <td className=''>{item.off}</td>
                    <td className='d-flex justify-content-center flex-column gap-2'>
                      <button onClick={() => getEditProduct(item._id)} className='btn btn-sm btn-primary'>edit</button>
                      <button className='btn btn-sm btn-danger'>remove</button>
                    </td>
                  </tr>
                )
              })
            }
          </tbody>
        </table>

        <div style={{ direction: 'ltr', alignSelf: 'center' }} className="row" >
          <ReactPaginate
            breakLabel="..."
            className='mystyle'
            nextLabel="بعد"
            onPageChange={(e) => changePage(e)}
            pageRangeDisplayed={1}
            pageCount={parseInt(totalPage)}
            previousLabel="قبل"
            renderOnZeroPageCount={null}
            containerClassName={'pagination'}
            pageClassName={'page-link'}
            previousClassName={'page-item'}
            previousLinkClassName={'page-link'}
            nextClassName={'page-item'}
            nextLinkClassName={'page-link'}
            activeClassName={'paginateactive'}
            breakClassName={'page-item'}
            breakLinkClassName={'page-link'}
            initialPage={currentPage}
          >
          </ReactPaginate>
        </div>


      </div>

    </div>
  )
}

export default AddProductContentComponent