import { faAngleDoubleUp } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";
import BannerComponent from "../components/BannerComponent";
import SearchBarComponent from "../components/SearchBarComponent";
import SimilarComponent from "../components/SimilarComponent";

const MainComponent = () => {
  const scrollTop = () => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  };

  return (
    <>
      <div className="relative">
        <div className="mt-3 border-b-2">
          <SearchBarComponent />
        </div>

        <div className="mt-10 px-7 mb-7">
          <BannerComponent />
        </div>

        <div className="mt-10 px-7 mb-7">
          <SimilarComponent />
        </div>
        
        <div className="mt-10 px-7 mb-7">
          <BannerComponent />
        </div>

        <button
          onDoubleClick={() => scrollTop()}
          type="button"
          className="fixed md:bottom-3 bottom-5 left-5 mt-auto focus:outline-none text-white bg-slate-700 hover:bg-slate-800 focus:ring-4 focus:ring-slate-300 font-medium rounded-lg text-sm px-2 py-1   dark:bg-slate-600 dark:hover:bg-slate-700 dark:focus:ring-slate-800"
        >
          <FontAwesomeIcon icon={faAngleDoubleUp} />
        </button>
      </div>
    </>
  );
};

export default MainComponent;
